import { withSnackbar } from "notistack";
import { useEffect, useState } from "react";
import { useParams ,useHistory } from "react-router";
import { useDispatch, useSelector } from "react-redux";
import { ROUTE_CONSTANTS } from "Shared";
import { getSaleRecordById } from "Redux/Actions/agentAction";
import moment from "moment";

function SalesRecord(props) {
  
   const {id} = useParams();
   const dispatch = useDispatch();
  const history = useHistory();
  
  const salesRecordList = useSelector((state) => state.Agents.salesRecord);
   const loading = useSelector((state)=>state.loading.loading)
   console.log("sales litst",salesRecordList)

  const ToasNotification=(message,type)=>{
    props.enqueueSnackbar(message, {
      variant: type,
      autoHideDuration: 2000,
      anchorOrigin: {
        vertical: "top",
        horizontal: "center",
      },
    });
 }
         
  const RouteHandler=()=>{
    history.push(ROUTE_CONSTANTS.SALES_AGENT);
  }

  const getSaleRecord = ()=>{
      dispatch( getSaleRecordById(id,(message,type)=>{
        ToasNotification(message,type) 
      }))
   }   


   
  //useEffect
  useEffect(() => {
    getSaleRecord();
  }, [id]);

  return (
    <>
    
      <div className="d-flex justify-content-between flex-wrap w-100 page-title">
        <h2 className="h4">Sales Record</h2>

        <div className="col_rht d-flex align-items-center">
          <div className="form-group mb-0">
            <div className="d-flex align-items-center">
             
            </div>
          </div>
          <button
            onClick={() => RouteHandler()}
            className="btn btn-lg btn-primary"
          >
           BACK
          </button>
        </div>
      </div>
      <div className="table-responsive custom_table">
        <table className="table table-borderless">
          <thead>
            <tr>
              <th>Purchase Date</th>
              <th>Land Coordinates</th>
              <th>Land Price</th>
              
            </tr>
          </thead>

          {salesRecordList?.map((value) => (
            <tbody key={value._id}>
              <tr>
                    <td>
                        <span className="time">
                            {moment(value.lands.dateOfPurchase).format("DD-MMM-YYYY")}
                        </span>
                   </td>
                <td>{`[ ${ value.lands.x } , ${value.lands.y }]`}</td>
                <td>{`${value.lands.lp} $`}</td>
                              
              </tr>
            </tbody>
          ))}

          {!salesRecordList.length && !loading && <h4 className="text-center"> No sales done by this Agent</h4>}
        </table>
      </div>
     
    </>
  );
}

export default withSnackbar(SalesRecord);
