import { withSnackbar } from "notistack";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router";
import { addSalesAgent_Admin, deleteAgent, getAgent, setActiveInActiveStatus } from "Redux/Actions/agentAction";
import { ICONS } from "Shared/Images";

import moment from "moment";
import "./style.scss";
import CustomModal from "Components/atoms/Modal";
import AddSalesAgent_Admin from "./AddSalesAgent_Admin";
import { Checkbox } from "@mui/material";
import EditSalesAgent_Admin from "./EditSalesAgent_Admin";
import { Link } from "react-router-dom";
import { ROUTE_CONSTANTS } from "Shared";

function SalesAgent(props) {
  const [modalOpen, setmodalOpen] = useState(false);
  const [error, setError] = useState(false);
  const [showEditModal, setEditModalView] = useState({
    add: false,
    edit: false,
    delete: false,
  });

  const [selectedAgent, setSelectedAgent] = useState({});

  const dispatch = useDispatch();
  const AgentsList = useSelector((state) => state.Agents.agents);

  const ToasNotification = (message, type) => {
    props.enqueueSnackbar(message, {
      variant: type,
      autoHideDuration: 2000,
      anchorOrigin: {
        vertical: "top",
        horizontal: "center",
      },
    });
  };

  const getAgentList = () => {
    dispatch(
      getAgent((message, type) => {
        ToasNotification(message, type);
      })
    );
  };

  const toggleModalView = () => {
    setmodalOpen((modalOpen) => !modalOpen);
  };

  const clearHandler = () => {
    setError(false);
  };

  //Add Agent Modal Handler
  const addHandler = () => {
    setEditModalView({ add: true, delete: false, edit: false });
    setmodalOpen(true);
  };

  // Edit Modal Handler
  const editHandler = (value) => {
    setSelectedAgent(value);
    setEditModalView({ add: false, delete: false, edit: true });
    setmodalOpen(true);
  };

  //Delete Prompt function
  const deleteHandler = () => {
    const payload = selectedAgent._id;

    dispatch(
      deleteAgent(payload, (message, type) => {
        getAgentList();
        ToasNotification(message, type);
      })
    );
    setmodalOpen(false);
    setEditModalView({ add: false, delete: false, edit: false });
  };

  //Delete Api hit function
  const deleteConfirmationPrompt = (value) => {
    setEditModalView({ add: false, delete: true, edit: false });
    setmodalOpen(true);
    setSelectedAgent(value);
  };

  //Adding Sale Agent
  const RegisterSalesAgent = (value) => {
    const payload = {
      userName: value.UserName,
      email: value.Email,
      //publicAddress: value.PublicAddress,
    };

    dispatch(
      addSalesAgent_Admin(payload, (message, type) => {
        getAgentList();
        ToasNotification(message, type);
      })
    );
    setEditModalView({ add: false, delete: false, edit: false });
    setmodalOpen(false);
  };

  //Active Inactive handler

  const setActiveInactive = (agentId, val) => {

    const payload ={
      agentId :agentId,
      status:val
    }
      dispatch(setActiveInActiveStatus(payload,(message,type)=>{
        getAgentList();
        ToasNotification(message, type);
      })) 

    
  };

  //useEffect
  useEffect(() => {
    getAgentList();
  }, []);

  return (
    <>
      <CustomModal isOpen={modalOpen} handleToggle={toggleModalView} clearHandler={clearHandler}>
        {showEditModal.add ? (
          <>
            <div className="modal-header justify-content-center">
              <h3 className="h4">Add New Agent</h3>
            </div>

            <div className="modal-body">
              <div className="row">
                <div className="col-12">
                  <AddSalesAgent_Admin hitDispatchForSalesRegister={RegisterSalesAgent} />
                </div>
              </div>
            </div>
          </>
        ) : showEditModal.edit ? (
          <>
            <div className="modal-header justify-content-center">
              <h3>Edit Agent</h3>
            </div>
            <div className="modal-body">
              <div className="row">
                <div className="col-12">
                  <EditSalesAgent_Admin selectedAgent={selectedAgent} />
                </div>
              </div>
            </div>
          </>
        ) : (
          <>
            <div className="modal-header justify-content-center">
              <label className="text-danger">Are you sure you want to delete this sale agent ?</label>
            </div>

            <div className="modal-footer justify-content-center">
              <button
                className="btn btn-md btn-cancel"
                onClick={() => {
                  toggleModalView();
                  setError(false);
                }}
              >
                No
              </button>
              <button
                className="btn btn-md btn-primary"
                onClick={() => {
                  deleteHandler();
                }}
              >
                Yes
              </button>
            </div>
          </>
        )}
      </CustomModal>

      <div className="d-flex justify-content-between flex-wrap w-100 page-title">
        <h2 className="h4">Manage Sales Agent</h2>

        <div className="col_rht d-flex align-items-center">
          <div className="form-group mb-0">
            <div className="d-flex align-items-center">
              {/*  <label className="sort-sec">Sort by</label> */}
              <input className="form-control" type="text" placeholder="search name, email"></input>
            </div>
          </div>
          <button onClick={() => addHandler()} className="btn btn-lg btn-primary">
            Add Agent
          </button>
        </div>
      </div>

      <div className="table-responsive custom_table">
        <table className="table table-borderless">
          <thead>
            <tr>
              <th>Name</th>
              <th>Email</th>
              <th>Created Date</th>
              <th>Agent Status</th>
              <th>Sales Record</th>
              <th>Actions</th>
            </tr>
          </thead>

          {AgentsList?.map((value) => (
            <tbody key={value._id}>
              <tr>
                <td>{value.userName}</td>
                <td>{value.email}</td>
                <td>
                  <span className="time">{moment(value.createdAt).format("DD-MMM-YYYY")}</span>
                </td>
                <td>
                  {value.isActive ? "Active" : "In Active"}
                  {/* <Checkbox color="success"  checked={value.isActive} onChange={()=>activeInactiveHandler(value._id)} /> */}
                </td>
                <td>
            
                  <Link to={`/admin/sales/${value._id}`}> View </Link>
                </td>
                <td>
               <div className="form-group">  
                
                { value.isActive ? <button className="btn btn-danger" onClick={() => setActiveInactive(value._id,false)}>
                     Set Inactive
                     </button>  
                     :
                     <button  className="btn btn-success" onClick={() => setActiveInactive(value._id,true)}>
                     Set Active
                     </button>  
                     }
                  <div className="action_group">
                    
                  
                    <button className="btn" onClick={() => deleteConfirmationPrompt(value)}>
                      <img src={ICONS.RemoveIcon} alt="Edit Icon" />
                    </button>
                  </div>
                  </div> 
                </td>
              </tr>
            </tbody>
          ))}
        </table>
      </div>
    </>
  );
}

export default withSnackbar(SalesAgent);
