
import { SET_USERS} from "../Actions/ActionType"

const initialState={
    users:[]
}

const ManageUsers =(state=initialState,action)=>{

 switch(action.type){
     
         case SET_USERS:
             return{
                 ...state,
                 users: action.payload
             }

            default:
                return state;
 }
}



export default ManageUsers;