import { withSnackbar } from "notistack";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router";
import { ICONS } from "Shared/Images";
import moment from "moment";
import "./style.scss";
import CustomModal from "Components/atoms/Modal";
import { getAgentSoldLandData } from 'Redux/Actions/agentAction';


function UserLands(props) {
  const dispatch = useDispatch();
  const salesAgentId = useSelector((state) => state.auth.salesAgentId);
  const userLands = useSelector((state)=>state.Agents.userLandData )
  const getUserDataList = () => {
    let data=salesAgentId
    dispatch(
      getAgentSoldLandData(data,(message, type) => {
        props.enqueueSnackbar(message, {
          variant: type,
          autoHideDuration: 2000,
          anchorOrigin: {
            vertical: "top",
            horizontal: "center",
          },
        });
      })
    );
  };

  useEffect(() => {
    getUserDataList();
  }, [])

  return (
    <>
           {userLands.length>=1 ? (<div className="table-responsive custom_table">
        <table className="table table-border">
          <thead>
            <tr>
              <th>User Id</th>
              <th>Date of Purchase</th>
              <th>Land Coordinates</th>
              <th>Land Price</th>
            </tr>
          </thead>

         {userLands?.map((value) => ( 
            <tbody key={value._id}>
              <tr>
                <td>{value?._id} </td>
                <td>
                  <span className="time">
                     {moment(value?.lands?.dateOfPurchase).format("DD-MMM-YYYY")}
                  </span>
                </td>
                <td>{value?.lands?.x},{value?.lands?.y} </td>
                <td>{value?.lands?.lp}</td>
              </tr>
            </tbody>
    ))} 
        </table>
      </div> ) 
      :
      <span className="display-3 text-md-center">
        No Data Availiable
      </span>
      }
    </>
  );
}

export default withSnackbar(UserLands)