import React, { useDebugValue, useState } from "react";
import { Formik, Field, Form, ErrorMessage } from "formik";
import { useHistory } from "react-router";
import { useLocation, useParams } from "react-router-dom";

import * as Yup from "yup";
import "./style.scss";
import { withSnackbar } from "notistack";
import { STRINGS, LABELS, PLACEHOLDERS, REGEX } from "Shared/Constants";
import { ROUTE_CONSTANTS } from "Shared/Routes";
import { IMAGES } from "Shared";
import { useDispatch } from "react-redux";
import { changePassword } from "Redux/Actions/Auth";
import { ControlPointSharp, Visibility, VisibilityOff } from "@material-ui/icons";

const ChangePassword = (props) => {
  const history = useHistory();
  const dispatch = useDispatch();
  const [showPassword, setShowPassword] = useState(false);
  const [showCPassword, setShowCPassword] = useState(false);
  const params = useParams();
  const [loginType, setLoginType] = useState();

  const submitHandler = (val) => {
    // alert("SUCCESS!! :-)\n\n" + JSON.stringify(val, null, 4));

    console.log("Token: ", params.token);

    let value = {
      token: params.token,
      password: val[STRINGS.PASSWORD],
    };

    dispatch(
      changePassword(value, (message, type) => {
        props.enqueueSnackbar(message, {
          variant: type,
          autoHideDuration: 2000,
          anchorOrigin: {
            vertical: "top",
            horizontal: "center",
          },
        });
      })
    );
  };

  const validationHandler = Yup.object({
    [STRINGS.PASSWORD]: Yup.string().required(STRINGS.PASSWORD_EMPTY),
    [STRINGS.CONFIRM_PASSWORD]: Yup.string()
      .oneOf([Yup.ref(STRINGS.PASSWORD), null], STRINGS.CONFIRM_PASSWORD_DOESNOT_MATCH)
      .required(STRINGS.CONFIRM_PASSWORD_EMPTY),
  });

  const changeRoute = () => {
    history.push(ROUTE_CONSTANTS.LOGIN);
  };

  return (
    <div className="front_main">
      <div className="row align-items-center no-gutters">
        <aside className="col-md-12">
          <div className="ls_form ">
            <a className="text-center d-block">
              <img height="60px" width="290px" src={IMAGES.LOGO} />
            </a>
            <hgroup className="login_title_group">
              <h1 className="h4">Reset Password</h1>
              <h2 className="h4">Reset and let's get going</h2>
            </hgroup>

            <Formik
              initialValues={{
                [STRINGS.PASSWORD]: STRINGS.EMPTY_STRING,
                [STRINGS.CONFIRM_PASSWORD]: STRINGS.EMPTY_STRING,
              }}
              validationSchema={validationHandler}
              onSubmit={submitHandler}
              render={({ errors, status, touched }) => (
                <Form>
                  <div className="form-group">
                    <label htmlFor={STRINGS.PASSWORD}>{LABELS.PASSWORD}</label>
                    <Field
                      name={STRINGS.PASSWORD}
                      type={showPassword ? "text" : "password"}
                      className={"form-control" + (errors[STRINGS.PASSWORD] && touched[STRINGS.PASSWORD] ? " is-invalid" : "")}
                    />
                    <div
                      className="visibility-Eye"
                      onClick={() => {
                        setShowPassword(!showPassword);
                      }}
                    >
                      {showPassword ? <VisibilityOff /> : <Visibility />}
                    </div>
                    <ErrorMessage name={STRINGS.PASSWORD} component="div" className="invalid-feedback" />
                  </div>
                  <div className="form-group">
                    <label htmlFor={STRINGS.CONFIRM_PASSWORD}>{LABELS.CONFIRM_PASSWORD}</label>
                    <Field
                      name={STRINGS.CONFIRM_PASSWORD}
                      type={showCPassword ? "text" : "password"}
                      className={"form-control" + (errors[STRINGS.CONFIRM_PASSWORD] && touched[STRINGS.CONFIRM_PASSWORD] ? " is-invalid" : "")}
                    />
                    <div
                      className="visibility-Eye"
                      onClick={() => {
                        setShowCPassword(!showCPassword);
                      }}
                    >
                      {showCPassword ? <VisibilityOff /> : <Visibility />}
                    </div>
                    <ErrorMessage name={STRINGS.CONFIRM_PASSWORD} component="div" className="invalid-feedback" />
                  </div>
                  <div className="form-group">
                    <button onClick={submitHandler} className="btn btn-primary mr-2">
                      Reset Password
                    </button>
                    <button onClick={changeRoute} className="btn mr-2 ml-2   btn-primary " type="button">
                      Login
                    </button>
                  </div>
                </Form>
              )}
            />
          </div>
        </aside>
      </div>
    </div>
  );
};

export default withSnackbar(ChangePassword);
