import Select from "react-select";

const SingleSelect = ({
  isMulti = false,
  label,
  name,
  isClearable = false,
  defaultValue,
  isSearchable = false,
  placeholder,
  options,
  value,
  handleChange = () => {},
  className = "",
  classNamePrefix = "",
  id= "",
  
}) => {
  
  
  return (
  
  
      
      <Select
      name={name}
        value={value}
        defaultValue={defaultValue}
        placeholder={placeholder}
        options={options}
        onChange={handleChange}
        className={className}
        classNamePrefix={classNamePrefix}
        isSearchable={isSearchable}
        isMulti={isMulti}
        isClearable={isClearable}
        key={id}
        
      />
   
    
  );
};

export default SingleSelect;
