import { takeLatest, all, put } from "@redux-saga/core/effects";
import { STRINGS, STATUS, STATUS_CODE, getRequest, postRequest, deleteRequest, putRequest, BASE_URL } from "Shared";

import { startLoader, stopLoader } from "Redux/Actions/commonCRUD";
import { GET_AGENTS, DELETE_AGENT, ADD_AGENT, GET_PENDING_APPROVAL, UPDATE_PENDING_APPROVAL, GET_SALES_RECORD_BY_ID, GET_AGENT_SOLD_LAND_DATA, SET_ACTIVE_INACTIVE_AGENT } from "Redux/Actions/ActionType";
import { getPendingApproval, setAgent, setPendingApproval, setSaleRecordById, setAgentSoldLandData } from "Redux/Actions/agentAction";

function* getAgentsFn({ callBack }) {
  try {
    yield put(startLoader());
    const response = yield getRequest({
      API: `${BASE_URL}/v1/salesAgent/list?status=1`,
    });
    if (response.status === 400) {
      callBack(response.data.message, "error");
      yield put(stopLoader());
    } else if (response.status === 401) {
      callBack(response.data.message, "error");
      yield put(stopLoader());
    } else if (response.status === 200) {
      yield put(stopLoader());
      callBack(response.data.message, "success");
      yield put(setAgent(response.data.data));
    }
  } catch (error) {
    callBack("Something went wrong", "error");
    console.log(error);
  } finally {
    yield put(stopLoader());
  }
}

function* deleteAgentsFn({ payload, callBack }) {
  try {
    yield put(startLoader());
    const response = yield deleteRequest({
      API: `${BASE_URL}/v1/salesAgent?salesAgentId=${payload}`,
    });
    if (response.status === 400) {
      callBack(response.data.msg, "error");
    } else if (response.status === 401) {
      callBack(response.data.msg, "error");
    } else if (response.status === 200) {
      callBack("Sales Agent Deleted Successfully", "success");
    }
  } catch (error) {
    callBack("Something went wrong", "error");
    console.log("Error");
  } finally {
    yield put(stopLoader());
  }
}

