function* setAcitveInactiveFn({ payload, callBack }) {
  try {
    yield put(startLoader());
    const response = yield putRequest({
      API: `${BASE_URL}/v1/salesAgent/active?salesAgentId=${payload.agentId}&isActive=${payload.status}`,
      DATA: payload,
    });

    if (response.status === 400) {
      callBack(response.data.message, "error");
      yield put(stopLoader());
    } else if (response.status === 401) {
      callBack(response.data.message, "error");
      yield put(stopLoader());
    } else if (response.status === 200) {
      yield put(stopLoader());
      callBack(response.data.message, "success");

      //yield put(setAgent(response.data.data))
    }
  } catch (error) {
    callBack("Something went wrong", "error");
    console.log(error);
  } finally {
    yield put(stopLoader());
  }
}

function* manageAgentsSagaWatcher() {
  yield all([
    takeLatest(GET_AGENTS, getAgentsFn),
    takeLatest(DELETE_AGENT, deleteAgentsFn),
    takeLatest(ADD_AGENT, addAgentFn),
    takeLatest(GET_PENDING_APPROVAL, getPendingApprovalFn),
    takeLatest(UPDATE_PENDING_APPROVAL, updatePedingApprovalsFn),
    takeLatest(GET_SALES_RECORD_BY_ID, getSalesRecordByIdFn),
    takeLatest(GET_AGENT_SOLD_LAND_DATA, getAgentSoldUserLandData),
    takeLatest(SET_ACTIVE_INACTIVE_AGENT,setAcitveInactiveFn)
  ]);
}

export default manageAgentsSagaWatcher;
