
import { takeLatest, all, put } from "@redux-saga/core/effects";
import {
  STRINGS,
  STATUS,
  STATUS_CODE,
  getRequest,
  postRequest,
  deleteRequest,
  putRequest,
  BASE_URL,
} from "Shared";

import { startLoader, stopLoader } from "Redux/Actions/commonCRUD";
import { GET_USERS } from "Redux/Actions/ActionType";
import { setUsers } from "Redux/Actions/usersAction";


function* getUsersFn({callBack}) {
  
    try {
      yield put(startLoader());
      const response = yield getRequest({
        API: `${BASE_URL}/v1/user/list`,
       // DATA: payload,
      });
  
     
      if(response.status===400){
        callBack(response.data.message, "error");
        yield put(stopLoader());  
      }
      else if(response.status===401){
        callBack(response.data.message, "error");
        yield put(stopLoader());
  
      }
  
      else if(response.status===200){
        yield put(stopLoader());
        callBack(response.data.message, "success");
       
        yield put(setUsers(response.data.data))
      }
    } catch (error) {
      console.log(error);
    }
  }

function* manageUserSagaWatcher() {
  yield all([
   takeLatest(GET_USERS, getUsersFn),
    
  ]);
}

export default manageUserSagaWatcher;
