import React, { useDebugValue, useState } from "react";
import FormikForm from "Components/atoms/Formik";
import { Formik, Field, Form, ErrorMessage } from "formik";
import TextField from "Components/atoms/TextField";
import { Route, useHistory } from "react-router";
import * as Yup from "yup";
import "./style.scss";
import { withSnackbar } from "notistack";
import { STRINGS, LABELS, PLACEHOLDERS, REGEX } from "Shared/Constants";
import { ROUTE_CONSTANTS } from "Shared/Routes";
import { IMAGES } from "Shared";
import { useDispatch } from "react-redux";
import { login, register } from "Redux/Actions/Auth";
import { ControlPointSharp, Visibility, VisibilityOff } from "@material-ui/icons";

function Register(props) {
  const history = useHistory();
  const dispatch = useDispatch();
  const [showPassword, setShowPassword] = useState(false);
  const [showCPassword, setShowCPassword] = useState(false);

  const [loginType, setLoginType] = useState();

  const submitHandler = (val) => {
    // alert("SUCCESS!! :-)\n\n" + JSON.stringify(val, null, 4));

    let value = {
      userName: val[STRINGS.USERNAME]?.toLowerCase(),
      email: val[STRINGS.EMAIL]?.toLowerCase(),
      password: val[STRINGS.PASSWORD],
      publicAddress: val[STRINGS.WALLET_ADD],
    };

    dispatch(
      register(value, (message, type) => {
        props.enqueueSnackbar(message, {
          variant: type,
          autoHideDuration: 2000,
          anchorOrigin: {
            vertical: "top",
            horizontal: "center",
          },
        });
      })
    );
  };

  const validationHandler = Yup.object({
    [STRINGS.USERNAME]: Yup.string().required(STRINGS.USERNAME_EMPTY),
    [STRINGS.EMAIL]: Yup.string().required(STRINGS.EMAIL_EMPTY).email(STRINGS.EMAIL_ERROR),
    [STRINGS.WALLET_ADD]: Yup.string().required(STRINGS.WALLET_ADD_EMPTY),
    [STRINGS.PASSWORD]: Yup.string().required(STRINGS.PASSWORD_EMPTY),
    [STRINGS.CONFIRM_PASSWORD]: Yup.string()
      .oneOf([Yup.ref(STRINGS.PASSWORD), null], STRINGS.CONFIRM_PASSWORD_DOESNOT_MATCH)
      .required(STRINGS.CONFIRM_PASSWORD_EMPTY),
  });

  const changeRoute = () => {
    history.push(ROUTE_CONSTANTS.LOGIN);
  };

  return (
    <div className="front_main">
      <div className="row align-items-center no-gutters">
        <aside className="col-md-12">
          <div className="ls_form ">
            <a className="text-center d-block">
              <img height="60px" width="290px" src={IMAGES.LOGO} />
            </a>
            <hgroup className="login_title_group">
              <h1 className="h4">Greetings,</h1>
              <h2 className="h4">Register and let's get going</h2>
            </hgroup>

            <Formik
              initialValues={{
                [STRINGS.USERNAME]: STRINGS.EMPTY_STRING,
                [STRINGS.EMAIL]: STRINGS.EMPTY_STRING,
                [STRINGS.WALLET_ADD]: STRINGS.EMPTY_STRING,
                [STRINGS.PASSWORD]: STRINGS.EMPTY_STRING,
                [STRINGS.CONFIRM_PASSWORD]: STRINGS.EMPTY_STRING,
              }}
              validationSchema={validationHandler}
              onSubmit={submitHandler}
              render={({ errors, status, touched }) => (
                <Form>
                  <div className="form-group">
                    <label htmlFor={STRINGS.USERNAME}>{LABELS.USERNAME}</label>
                    <Field name={STRINGS.USERNAME} type="text" className={"form-control" + (errors[STRINGS.USERNAME] && touched[STRINGS.USERNAME] ? " is-invalid" : "")} />
                    <ErrorMessage name={STRINGS.USERNAME} component="div" className="invalid-feedback" />
                  </div>
                  <div className="form-group">
                    <label htmlFor={STRINGS.EMAIL}>{LABELS.EMAIL}</label>
                    <Field name={STRINGS.EMAIL} type="text" className={"form-control" + (errors[STRINGS.EMAIL] && touched[STRINGS.EMAIL] ? " is-invalid" : "")} />
                    <ErrorMessage name={STRINGS.EMAIL} component="div" className="invalid-feedback" />
                  </div>
                  <div className="form-group">
                    <label htmlFor={STRINGS.WALLET_ADD}>{LABELS.WALLET_ADD}</label>
                    <Field name={STRINGS.WALLET_ADD} type="text" className={"form-control" + (errors[STRINGS.WALLET_ADD] && touched[STRINGS.WALLET_ADD] ? " is-invalid" : "")} />
                    <ErrorMessage name={STRINGS.WALLET_ADD} component="div" className="invalid-feedback" />
                  </div>
                  <div className="form-group">
                    <label htmlFor={STRINGS.PASSWORD}>{LABELS.PASSWORD}</label>
                    <Field
                      name={STRINGS.PASSWORD}
                      type={showPassword ? "text" : "password"}
                      className={"form-control" + (errors[STRINGS.PASSWORD] && touched[STRINGS.PASSWORD] ? " is-invalid" : "")}
                    />
                    <div
                      className="visibility-Eye"
                      onClick={() => {
                        setShowPassword(!showPassword);
                      }}
                    >
                      {showPassword ? <VisibilityOff /> : <Visibility />}
                    </div>
                    <ErrorMessage name={STRINGS.PASSWORD} component="div" className="invalid-feedback" />
                  </div>
                  <div className="form-group">
                    <label htmlFor={STRINGS.CONFIRM_PASSWORD}>{LABELS.CONFIRM_PASSWORD}</label>
                    <Field
                      name={STRINGS.CONFIRM_PASSWORD}
                      type={showCPassword ? "text" : "password"}
                      className={"form-control" + (errors[STRINGS.CONFIRM_PASSWORD] && touched[STRINGS.CONFIRM_PASSWORD] ? " is-invalid" : "")}
                    />
                    <div
                      className="visibility-Eye"
                      onClick={() => {
                        setShowCPassword(!showCPassword);
                      }}
                    >
                      {showCPassword ? <VisibilityOff /> : <Visibility />}
                    </div>
                    <ErrorMessage name={STRINGS.CONFIRM_PASSWORD} component="div" className="invalid-feedback" />
                  </div>
                  <div className="form-group">
                    <button type="submit" className="btn btn-primary mr-2">
                      Register
                    </button>
                    <button type="reset" className="btn btn-secondary">
                      Reset
                    </button>

                    <button onClick={changeRoute} className="btn mr-2 ml-2   btn-primary " type="button">
                      {"Already a user?"}
                    </button>
                  </div>
                </Form>
              )}
            />
          </div>
        </aside>
      </div>
    </div>
  );
}

export const Register_Form = withSnackbar(Register);
