import { all, fork } from "redux-saga/effects";

import watchAuth from "./Auth";
import filterSagaWatcher from "./filterCRUD";
import commonSagaWatcher from "./commonCRUD";
import manageUserSagaWatcher from "./manageUsers"
import manageAgentsSagaWatcher from "./manageAgents"



function* rootSaga() {
  yield all([fork(watchAuth),fork(commonSagaWatcher),fork(filterSagaWatcher) , fork(manageUserSagaWatcher) , fork(manageAgentsSagaWatcher)]);
}

export default rootSaga;
