import { SET_AUTHTOKEN, SET_SALES_AGENT_ID, SET_AGENT_DASHBOARD_DATA } from "./../Actions/ActionType";
import { ACTION_STATES } from "../Actions/ActionStates";

const initialState = {
  token: null,
  role: null,
  salesAgentId: null,
  agentDashboard: null,
};
const authReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_AUTHTOKEN: {
      return {
        ...state,
        token: action.token,
        role: action.role,
      };
    }

    case SET_SALES_AGENT_ID: {
      return {
        ...state,
        salesAgentId: action.payload,
      };
    }
    case SET_AGENT_DASHBOARD_DATA: {
      console.log("egfdjdf", action);
      return {
        ...state,
        agentDashboard: action.payload,
      };
    }

    default: {
      return state;
    }
  }
};

export default authReducer;
