import { SET_AUTHTOKEN, SET_EMAIL } from "./ActionType";

export const LOGIN = "LOGIN";
export const REGISTER = "REGISTER";
export const FORGOT_PASSWORD = "FORGOT_PASSWORD";
export const LOGOUT = "LOGOUT";
export const CHANGE_PASSWORD = "CHANGE_PASSWORD";

export const login = (payload, callback) => {
  return {
    type: LOGIN,
    payload,
    callback,
  };
};

export const register = (payload, callback) => {
  return {
    type: REGISTER,
    payload,
    callback,
  };
};

export const forgotPassword = (payload, callback) => {
  return {
    type: FORGOT_PASSWORD,
    payload,
    callback,
  };
};

export const changePassword = (payload, callback) => {
  return {
    type: CHANGE_PASSWORD,
    payload,
    callback,
  };
};

export const logout = () => {
  return {
    type: LOGOUT,
  };
};

export const setAuthToken = (token, role) => {
  return {
    type: SET_AUTHTOKEN,
    token: token,
    role: role,
  };
};

export const setEmail = (data) => {
  return {
    type: SET_EMAIL,
    data,
  };
};
