import { withSnackbar } from "notistack";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";

import {  getPendingApproval, updatePendingApprovalStatus } from "Redux/Actions/agentAction";
import { ICONS } from "Shared/Images";
import Select from "react-select";

import moment from "moment";
import "./style.scss";
import SingleSelect from "Components/atoms/SingleSelect";

const opt = [
    {
      label: " APPROVED",
      value: true,
    },
/*     {
      label: "PENDING",
      value:3
    }, */
    {
        label:"REJECTED",
        value:false
    }
  ];
  

function AgentStatus(props) {
  

  const dispatch = useDispatch();
  const loading = useSelector((state)=>state.loading.loading)
  const pendingAgentList = useSelector((state) => state.Agents.pendingApproval);
  const [agentApprovalStatus,setAgentApprovalStatus] = useState({
    
        label: " Pending",
        value: 3,
      }
   )
  
  

  const ToasNotification=(message,type)=>{
    props.enqueueSnackbar(message, {
      variant: type,
      autoHideDuration: 2000,
      anchorOrigin: {
        vertical: "top",
        horizontal: "center",
      },
    });

  }


  //getting pending list of agent who are not aaproved yet
  const getPendingApprovalList = () => {

    dispatch(
        getPendingApproval((message, type) => {
           ToasNotification(message,type)
      })
    );
  };



  //updating aaproval status of agent
  const updateApprovalStatus = (approvalStatus, agentId)=>{
      
      const payload = {
        salesAgentId:agentId,
        approved:approvalStatus
      }
      dispatch(updatePendingApprovalStatus(payload,(message,type)=>{
        ToasNotification(message,type)
        getPendingApprovalList();
      }))
  }
 
  
  //useEffect
  useEffect(() => {
    getPendingApprovalList();
  }, []);

  return (
    <>
     

      <div className="d-flex justify-content-between flex-wrap w-100 page-title">
        <h2 className="h4">Pending Approvals</h2>

        <div className="col_rht d-flex align-items-center">
          <div className="form-group mb-0">
            <div className="d-flex align-items-center">
             
             {/*  <input
                className="form-control"
                type="text"
                placeholder="search name, email"
              ></input> */}
            </div>
          </div>
         {/*  <button
            onClick={() => addHandler()}
            className="btn btn-lg btn-primary"
          >
            Add Agent
          </button> */}
        </div>
      </div>

      <div className="table-responsive custom_table">
     {pendingAgentList.length ?   <table className="table table-borderless">
          <thead>
            <tr>
              <th>Name</th>
              <th>Email</th>
              <th>Created Date</th>
              <th>PublicAddress</th>
              <th>Set Status</th>
            </tr>
          </thead>

          { pendingAgentList?.map((value) => (
            <tbody key={value._id}>
              <tr>
                <td>{value.userName}</td>
                <td>{value.email}</td>
                <td>
                  <span className="time">
                    {moment(value.createdAt).format("DD-MMM-YYYY")}
                  </span>
                </td>
                <td>
                  {value.publicAddress.slice(0,3)+'...'+value.publicAddress.slice(5,8) }
                   
                </td>
                   
                   <td>
                       <SingleSelect
                      // defaultValue={opt[1]}
                      placeholder="Select"
                            key={value._id}
                            //value={agentApprovalStatus}
                            handleChange={(e) => {
                                setAgentApprovalStatus(e);
                                console.log(e)
                                updateApprovalStatus(e.value,value._id)
                            }}
                            className=""
                            classNamePrefix="react-select-dropdown-prefix"
                            options={opt}
                       />
                   </td>
              </tr>
            </tbody>
          ))}
        </table> : <h3 className="text-center">{!loading && "No pending approval found"}</h3>}
      </div>
    </>
  );
}

export default withSnackbar(AgentStatus);
