import React, { useState } from "react";
import TextField from "Components/atoms/TextField";
import { Form, Formik } from "formik";
import * as Yup from "yup";
import "./style.scss";
import { STRINGS, LABELS, PLACEHOLDERS, REGEX } from "Shared/Constants";
import DropDown from "Components/atoms/Dropdown";

const opt = [
  {
    label: "Active",
    value: true,
  },
  {
    label: "In Active",
    value: false,
  },
];



const EditSalesAgent_Admin = ({ hitDispatchForSalesRegister = () => {}, selectedAgent,}) => {


  const val = opt.filter(item=>{
  return item.value==selectedAgent.value
  })
  

  
  const [agentStatus, setAgentStatus] = useState();

  const validationHandler = Yup.object({
    [STRINGS.EMAIL]: Yup.string()
      .required(STRINGS.EMAIL_EMPTY)
      .email(STRINGS.EMAIL_ERROR),
    [STRINGS.AGENT_STATUS]: Yup.object().required("Required").nullable(),
    [STRINGS.USERNAME]: Yup.string().required(STRINGS.USERNAME_EMPTY),
  });

  const submitHandler = (value) => {
    hitDispatchForSalesRegister(value);
  };

  return (
    <Formik
      onSubmit={submitHandler}
      enableReinitialize
      initialValues={{
        [STRINGS.USERNAME]: selectedAgent.userName||null,
        [STRINGS.EMAIL]: selectedAgent.email||null,
        [STRINGS.AGENT_STATUS]:opt.filter(item=>item.value==selectedAgent.isActive) ,
        [STRINGS.PUBLIC_ADDRESS]: selectedAgent.publicAddress,
      }}
      validationSchema={validationHandler}
    >
      {(props) => (
        <>
          <Form>
            <div className="form-group">
              <TextField
                label={LABELS.USERNAME}
                name={STRINGS.USERNAME}
                placeholder={PLACEHOLDERS.USERNAME}
                type="text"
              />
            </div>
            <div className="form-group">
              <TextField
                label={LABELS.EMAIL}
                name={STRINGS.EMAIL}
                placeholder={PLACEHOLDERS.EMAIL}
                type="text"
              />
            </div>

          
            <div className="form-group">
              <DropDown
                value={agentStatus}
                name={STRINGS.AGENT_STATUS}
                label={LABELS.AGENT_STATUS}
                placeholder={PLACEHOLDERS.AGENT_STATUS}
                handleChange={(e) => {
                  setAgentStatus(e);
                  props.setFieldValue(STRINGS.AGENT_STATUS, e);
                }}
                className="custom_select"
                classNamePrefix="react-select-dropdown-prefix"
                options={opt}
              />
            </div>

            <div className="form-group">
              <TextField
                label={LABELS.PUBLIC_ADDRESS}
                name={STRINGS.PUBLIC_ADDRESS}
                placeholder={PLACEHOLDERS.PUBLIC_ADDRESS}
                type="text"
              />
            </div>

            <button
              className="btn  mt-3 btn-lg btn-primary w-100"
              type="submit"
            >
              {STRINGS.ADD_AGENT}
            </button>
          </Form>
        </>
      )}
    </Formik>
  );
};

export default EditSalesAgent_Admin;
