import React, { useDebugValue, useState } from "react";
import FormikForm from "Components/atoms/Formik";
import TextField from "Components/atoms/TextField";
import { useHistory } from "react-router";
import * as Yup from "yup";
import "./style.scss";
import { withSnackbar } from "notistack";
import { STRINGS, LABELS, PLACEHOLDERS, REGEX } from "Shared/Constants";
import { ROUTE_CONSTANTS } from "Shared/Routes";
import { IMAGES } from "Shared";
import { useDispatch } from "react-redux";
import { forgotPassword } from "Redux/Actions/Auth";

const ForgotPassword = (props) => {
  const history = useHistory();
  const dispatch = useDispatch();

  const [loginType, setLoginType] = useState();

  const submitHandler = (val) => {
    let value = {
      email: val.Email?.toLowerCase(),
    };

    dispatch(
      forgotPassword(value, (message, type) => {
        props.enqueueSnackbar(message, {
          variant: type,
          autoHideDuration: 2000,
          anchorOrigin: {
            vertical: "top",
            horizontal: "center",
          },
        });
      })
    );
  };

  const validationHandler = Yup.object({
    [STRINGS.EMAIL]: Yup.string().required(STRINGS.EMAIL_EMPTY).email(STRINGS.EMAIL_ERROR),
  });

  const goToLogin = () => {
    history.push(ROUTE_CONSTANTS.LOGIN);
  };

  return (
    <div className="front_main">
      <div className="row align-items-center no-gutters">
        {/*   <aside className="col-md-6   ">
                      <figure className="mb-0"><img height="100%" width="100%" className="img-thumbnail" src={IMAGES.LOGIN_BG}   /></figure>
                  </aside>
    */}
        <aside className="col-md-12">
          <div className="ls_form ">
            <a className="text-center d-block">
              <img height="60px" width="290px" src={IMAGES.LOGO} />
            </a>
            <hgroup className="login_title_group">
              <h1 className="h4">Forgot Password,</h1>
              <h2 className="h4">Please enter your registered email</h2>
            </hgroup>

            <FormikForm
              onSubmit={submitHandler}
              initialValues={{
                [STRINGS.EMAIL]: STRINGS.EMPTY_STRING,
              }}
              validationSchema={validationHandler}
            >
              <div className="form-group">
                <TextField label={LABELS.EMAIL} name={STRINGS.EMAIL} placeholder={PLACEHOLDERS.EMAIL} type="text" />
              </div>

              <button className="btn  mt-3 btn-lg btn-primary w-100" type="submit">
                Send Verification Link
              </button>
              <button
                onClick={() => {
                  history.push(ROUTE_CONSTANTS.LOGIN);
                }}
                className="btn  mt-3 btn-lg btn-primary w-100"
              >
                Go Back
              </button>
              {/* <button onClick={goToLogin} className="btn  mt-3 btn-lg btn-primary w-100">
                Go Back
              </button> */}
            </FormikForm>
          </div>
        </aside>
      </div>
    </div>
  );
};

export default withSnackbar(ForgotPassword);
