import { takeLatest, all, put } from "redux-saga/effects";
import { LOGIN, REGISTER, LOGOUT, setAuthToken, setUserId, FORGOT_PASSWORD, CHANGE_PASSWORD } from "../Actions/Auth";
import { login, logout } from "Services/Api/Auth";
import requestSaga from "Shared/RequestSaga";
import { BASE_URL, postRequest, updateAuthToken } from "Shared";
import { startLoader, stopLoader } from "Redux/Actions/commonCRUD";
import { yellow } from "@mui/material/colors";
import { setSalesAgentId } from "Redux/Actions/agentAction";
import { setAgentDashboardData } from "../Actions/agentAction";

function* loginAuth({ payload, callback }) {
  try {
    yield put(startLoader());
    const response = yield postRequest({
      API: `${BASE_URL}/v1/salesAgent/login`,
      DATA: payload,
    });

    if (response.status === 400) {
      callback(response.data.message, "error");
      yield put(stopLoader());
    } else if (response.status === 401) {
      callback(response.data.message, "error");
      yield put(stopLoader());
    } else if (response.status === 403) {
      callback(response.data.message, "error");
      yield put(stopLoader());
    } else if (response.status === 200) {
      console.log(response.data.data);
      yield put(stopLoader());
      callback(response.data.message, "success");
      updateAuthToken(response.data.data.token);
      yield put(setAuthToken(response.data.data.token, response.data.data.user.role));
      if (response.data.data.user.role == 2) {
        yield put(setSalesAgentId(response.data.data.user._id));
        yield put(setAgentDashboardData(response.data.data));
      }
    }
  } catch (error) {
    callback("Something went wrong", "error");
    console.log(error);
  } finally {
    yield put(stopLoader());
  }
}

