function* watchAuth() {
  yield all([
    takeLatest(LOGIN, loginAuth),
    takeLatest(REGISTER, registerAuth),
    takeLatest(FORGOT_PASSWORD, forgotPasswordAuth),
    takeLatest(CHANGE_PASSWORD, resetPasswordAuth),
    // takeLatest(LOGOUT, requestSaga, logout),
  ]);
}

export default watchAuth;
