import {
  ADD_AGENT,
  SET_SALES_AGENT_ID,
  SET_AGENT_DASHBOARD_DATA,
  DELETE_AGENT,
  GET_AGENTS,
  GET_PENDING_APPROVAL,
  GET_AGENT_SOLD_LAND_DATA,
  SET_AGENT_SOLD_LAND_DATA,
  SET_AGENTS,
  SET_PENDING_APPROVAL,
  GET_SALES_RECORD_BY_ID,
  SET_SALES_RECORD_BY_ID,
  UPDATE_PENDING_APPROVAL,
  SET_ACTIVE_INACTIVE_AGENT,
} from "./ActionType";

export const getAgent = (callBack) => {
  return {
    type: GET_AGENTS,
    callBack: callBack,
  };
};

export const setAgent = (payload) => {
  return {
    type: SET_AGENTS,
    payload: payload,
  };
};

export const deleteAgent = (payload, callBack) => {
  return {
    type: DELETE_AGENT,
    payload: payload,
    callBack: callBack,
  };
};

export const setAgentSoldLandData = (payload) => {
  return {
    type: SET_AGENT_SOLD_LAND_DATA,
    payload: payload,
  };
};

export const addSalesAgent_Admin = (payload, callBack) => {
  return {
    type: ADD_AGENT,
    payload: payload,
    callBack: callBack,
  };
};

