import { SET_AGENTS, SET_PENDING_APPROVAL, SET_AGENT_SOLD_LAND_DATA, SET_SALES_RECORD_BY_ID } from "../Actions/ActionType";

const initialState = {
  agents: [],
  pendingApproval: [],
  userLandData: [],
  salesRecord: [],
};

const ManageAgents = (state = initialState, action) => {
  switch (action.type) {
    case SET_AGENTS:
      return {
        ...state,
        agents: action.payload,
      };
    case SET_AGENT_SOLD_LAND_DATA:
      return {
        ...state,
        userLandData: action.payload,
      };
    case SET_SALES_RECORD_BY_ID:
      return {
        ...state,
        salesRecord: action.payload,
      };
    case SET_PENDING_APPROVAL:
      return {
        ...state,
        pendingApproval: action.payload,
      };

    default:
      return state;
  }
};

export default ManageAgents;
